import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { useNavigate } from 'react-router-dom';
import { 
  CreditCard, 
  Plus, 
  History, 
  TrendingUp, 
  Zap, 
  CheckCircle,
  Clock,
  AlertCircle,
  Coins
} from 'lucide-react';
import { toast } from 'sonner';
import axiosInstance from '@/api/axios/axiosInstance';
import CreditBalance from '@/components/Credits/CreditBalance';
import PurchaseCreditsModal from '@/components/Credits/PurchaseCreditsModal';
import CreditUsageAnalytics from '@/components/Credits/CreditUsageAnalytics';

const UserCreditPortal = () => {
  const navigate = useNavigate();
  const [showPurchaseModal, setShowPurchaseModal] = useState(false);
  const [creditData, setCreditData] = useState(null);
  const [transactions, setTransactions] = useState([]);
  const [packages, setPackages] = useState([]);
  const [loading, setLoading] = useState(true);
  const [refreshKey, setRefreshKey] = useState(0);

  // Fetch all credit data
  const fetchCreditData = async () => {
    try {
      setLoading(true);
      const [balanceRes, transactionsRes, packagesRes] = await Promise.all([
        axiosInstance.get('/api/credits/balance'),
        axiosInstance.get('/api/credits/transactions?limit=10'),
        axiosInstance.get('/api/credits/packages')
      ]);

      setCreditData(balanceRes.data.data);
      setTransactions(transactionsRes.data.data.transactions || []);
      setPackages(packagesRes.data.data || []);
    } catch (error) {
      console.error('Error fetching credit data:', error);
      toast.error('Failed to load credit information');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchCreditData();
    
    // Listen for credit balance updates
    const handleCreditUpdate = () => {
      fetchCreditData();
    };
    
    window.addEventListener('creditBalanceUpdate', handleCreditUpdate);
    
    return () => {
      window.removeEventListener('creditBalanceUpdate', handleCreditUpdate);
    };
  }, [refreshKey]);

  const handlePurchaseSuccess = (paymentIntent) => {
    // Show success notification
    toast.success('ðŸŽ‰ Purchase successful! Credits added to your account.');
    
    // Refresh all data
    setRefreshKey(prev => prev + 1);
    
    // Close modal
    setShowPurchaseModal(false);
    
    // Trigger global credit balance update
    window.dispatchEvent(new CustomEvent('creditBalanceUpdate'));
  };

  const getTransactionIcon = (type) => {
    switch (type) {
      case 'purchase':
        return <Plus className="h-4 w-4 text-green-500" />;
      case 'usage':
        return <Zap className="h-4 w-4 text-blue-500" />;
      case 'refund':
        return <CheckCircle className="h-4 w-4 text-purple-500" />;
      case 'adjustment':
        return <AlertCircle className="h-4 w-4 text-orange-500" />;
      default:
        return <Coins className="h-4 w-4 text-gray-500" />;
    }
  };

  const getTransactionColor = (type) => {
    switch (type) {
      case 'purchase':
        return 'text-green-600 bg-green-50 border-green-200';
      case 'usage':
        return 'text-blue-600 bg-blue-50 border-blue-200';
      case 'refund':
        return 'text-purple-600 bg-purple-50 border-purple-200';
      case 'adjustment':
        return 'text-orange-600 bg-orange-50 border-orange-200';
      default:
        return 'text-gray-600 bg-gray-50 border-gray-200';
    }
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Credit Portal</h1>
          <p className="text-gray-600 mt-1">Manage your credits and track usage</p>
        </div>
        <Button
          onClick={() => window.location.href = '/purchase-credits'}
          className="bg-blue-600 hover:bg-blue-700"
        >
          <Plus className="h-4 w-4 mr-2" />
          Buy Credits
        </Button>
      </div>

      {/* Credit Balance Card */}
      <Card className="bg-gradient-to-r from-blue-50 to-indigo-50 border-blue-200">
        <CardContent className="p-6">
          <div className="flex items-center justify-between">
            <div>
              <h2 className="text-lg font-semibold text-gray-900 mb-2">Current Balance</h2>
              <CreditBalance variant="full" />
            </div>
            <div className="text-right">
              <div className="text-sm text-gray-600 mb-1">Total Used</div>
              <div className="text-2xl font-bold text-gray-900">
                {creditData?.total_credits_used?.toLocaleString() || '0'}
              </div>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Main Content Tabs */}
      <Tabs defaultValue="overview" className="space-y-6">
        <TabsList className="grid w-full grid-cols-3">
          <TabsTrigger value="overview" className="flex items-center gap-2">
            <TrendingUp className="h-4 w-4" />
            Overview
          </TabsTrigger>
          <TabsTrigger value="transactions" className="flex items-center gap-2">
            <History className="h-4 w-4" />
            Transactions
          </TabsTrigger>
          <TabsTrigger value="packages" className="flex items-center gap-2">
            <CreditCard className="h-4 w-4" />
            Packages
          </TabsTrigger>
        </TabsList>

        {/* Overview Tab */}
        <TabsContent value="overview" className="space-y-6">
          <CreditUsageAnalytics key={refreshKey} />
        </TabsContent>

        {/* Transactions Tab */}
        <TabsContent value="transactions" className="space-y-6">
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <History className="h-5 w-5" />
                Recent Transactions
              </CardTitle>
            </CardHeader>
            <CardContent>
              {transactions.length === 0 ? (
                <div className="text-center py-8 text-gray-500">
                  <Clock className="h-12 w-12 mx-auto mb-4 opacity-50" />
                  <p>No transactions yet</p>
                  <p className="text-sm">Your credit transactions will appear here</p>
                </div>
              ) : (
                <div className="space-y-3">
                  {transactions.map((transaction) => (
                    <div 
                      key={transaction.id} 
                      className="flex items-center justify-between p-4 border rounded-lg hover:bg-gray-50"
                    >
                      <div className="flex items-center gap-3">
                        {getTransactionIcon(transaction.transaction_type)}
                        <div>
                          <div className="font-medium text-gray-900">
                            {transaction.description || `${transaction.transaction_type} transaction`}
                          </div>
                          <div className="text-sm text-gray-500">
                            {formatDate(transaction.created_at)}
                          </div>
                        </div>
                      </div>
                      <div className="text-right">
                        <Badge 
                          variant="outline" 
                          className={getTransactionColor(transaction.transaction_type)}
                        >
                          {transaction.transaction_type === 'usage' ? '-' : '+'}
                          {Math.abs(transaction.credit_amount).toLocaleString()} credits
                        </Badge>
                      </div>
                    </div>
                  ))}
                </div>
              )}
            </CardContent>
          </Card>
        </TabsContent>

        {/* Packages Tab */}
        <TabsContent value="packages" className="space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {packages.map((pkg) => (
              <Card key={pkg.id || pkg.package_id} className="relative hover:shadow-lg transition-shadow">
                {(pkg.bonus_credits || 0) > 0 && (
                  <div className="absolute -top-2 -right-2 bg-green-500 text-white text-xs px-2 py-1 rounded-full">
                    +{pkg.bonus_credits} Bonus
                  </div>
                )}
                <CardHeader>
                  <CardTitle className="text-lg">{pkg.name}</CardTitle>
                  <div className="text-3xl font-bold text-blue-600">
                    ${pkg.price_dollars || (pkg.price_cents / 100).toFixed(2)}
                  </div>
                </CardHeader>
                <CardContent>
                  <div className="space-y-3">
                    <div className="flex justify-between">
                      <span className="text-gray-600">Base Credits:</span>
                      <span className="font-medium">{(pkg.credits_amount || 0).toLocaleString()}</span>
                    </div>
                    {(pkg.bonus_credits || 0) > 0 && (
                      <div className="flex justify-between">
                        <span className="text-gray-600">Bonus Credits:</span>
                        <span className="font-medium text-green-600">+{(pkg.bonus_credits || 0).toLocaleString()}</span>
                      </div>
                    )}
                    <div className="flex justify-between border-t pt-3">
                      <span className="font-semibold">Total Credits:</span>
                      <span className="font-bold text-blue-600">{(pkg.total_credits || 0).toLocaleString()}</span>
                    </div>
                    <div className="text-sm text-gray-500 text-center">
                      ${((pkg.price_dollars || pkg.price_cents / 100) / (pkg.total_credits || 1)).toFixed(4)} per credit
                    </div>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </TabsContent>
      </Tabs>

      {/* Purchase Modal */}
      {showPurchaseModal && (
        <PurchaseCreditsModal
          isOpen={showPurchaseModal}
          onClose={() => setShowPurchaseModal(false)}
          onSuccess={handlePurchaseSuccess}
        />
      )}
    </div>
  );
};

export default UserCreditPortal;
