import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { 
  BarChart, 
  Bar, 
  XAxis, 
  YAxis, 
  CartesianGrid, 
  Tooltip, 
  ResponsiveContainer,
  PieChart,
  Pie,
  Cell,
  LineChart,
  Line
} from 'recharts';
import { 
  TrendingUp, 
  TrendingDown, 
  Phone, 
  MessageSquare, 
  FileText, 
  Zap,
  Calendar,
  Activity
} from 'lucide-react';
import { toast } from 'sonner';
import axiosInstance from '@/api/axios/axiosInstance';

const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042', '#8884D8'];

const CreditUsageAnalytics = ({ days = 30 }) => {
  const [analytics, setAnalytics] = useState(null);
  const [loading, setLoading] = useState(true);
  const [selectedPeriod, setSelectedPeriod] = useState(days);

  useEffect(() => {
    fetchAnalytics();
  }, [selectedPeriod]);

  const fetchAnalytics = async () => {
    try {
      setLoading(true);
      const response = await axiosInstance.get(`/api/credits/usage/analytics?days=${selectedPeriod}`);

      // Validate response structure
      const data = response.data.data;
      if (!data) {
        console.warn('No analytics data received from API');
        setAnalytics(null);
        return;
      }

      // Ensure required properties exist with defaults
      const validatedData = {
        summary: data.summary || {},
        dailyTransactions: Array.isArray(data.dailyTransactions) ? data.dailyTransactions : [],
        operationSummary: Array.isArray(data.operationSummary) ? data.operationSummary : [],
        peakHours: Array.isArray(data.peakHours) ? data.peakHours : [],
        period: data.period || `${selectedPeriod} days`
      };

      setAnalytics(validatedData);
    } catch (error) {
      console.error('Error fetching usage analytics:', error);
      toast.error('Failed to load usage analytics');
      setAnalytics(null);
    } finally {
      setLoading(false);
    }
  };

  const getOperationIcon = (operationType) => {
    switch (operationType) {
      case 'vapi_call':
        return <Phone className="h-4 w-4" />;
      case 'message':
        return <MessageSquare className="h-4 w-4" />;
      case 'file_upload':
        return <FileText className="h-4 w-4" />;
      case 'workflow_execution':
        return <Zap className="h-4 w-4" />;
      default:
        return <Activity className="h-4 w-4" />;
    }
  };

  const formatOperationType = (type) => {
    if (!type) return 'Unknown';
    return type.split('_').map(word =>
      word.charAt(0).toUpperCase() + word.slice(1)
    ).join(' ');
  };

  if (loading) {
    return (
      <div className="space-y-6">
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
          {[...Array(4)].map((_, i) => (
            <Card key={i}>
              <CardContent className="p-6">
                <div className="animate-pulse">
                  <div className="h-4 bg-gray-200 rounded w-3/4 mb-2"></div>
                  <div className="h-8 bg-gray-200 rounded w-1/2"></div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      </div>
    );
  }

  if (!analytics) {
    return (
      <Card>
        <CardContent className="p-6 text-center">
          <p className="text-gray-500">No usage data available</p>
        </CardContent>
      </Card>
    );
  }

  // Handle case where analytics is an empty object or missing required properties
  if (!analytics.summary && !analytics.dailyTransactions && !analytics.operationSummary) {
    return (
      <Card>
        <CardContent className="p-6 text-center">
          <p className="text-gray-500">No usage data available for the selected period</p>
        </CardContent>
      </Card>
    );
  }

  const { summary, dailyTransactions, operationSummary, peakHours } = analytics;

  // Prepare data for charts with null checks
  const dailyData = (dailyTransactions || []).reduce((acc, transaction) => {
    const date = transaction.date;
    const existing = acc.find(item => item.date === date);

    if (existing) {
      existing[transaction.type] = (existing[transaction.type] || 0) + parseFloat(transaction.total_amount);
    } else {
      acc.push({
        date,
        [transaction.type]: parseFloat(transaction.total_amount)
      });
    }

    return acc;
  }, []);

  const operationData = (operationSummary || []).map(op => ({
    name: formatOperationType(op.operation_type),
    credits: parseFloat(op.total_credits),
    operations: parseInt(op.operation_count)
  }));

  return (
    <div className="space-y-6">
      {/* Period Selector */}
      <div className="flex justify-between items-center">
        <h2 className="text-2xl font-bold">Usage Analytics</h2>
        <div className="flex space-x-2">
          {[7, 30, 90].map(period => (
            <Button
              key={period}
              variant={selectedPeriod === period ? "default" : "outline"}
              size="sm"
              onClick={() => setSelectedPeriod(period)}
            >
              {period} days
            </Button>
          ))}
        </div>
      </div>

      {/* Summary Cards */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <Card>
          <CardContent className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Total Credits Used</p>
                <p className="text-2xl font-bold text-red-600">
                  {parseFloat(summary?.total_credits_consumed || 0).toLocaleString()}
                </p>
              </div>
              <TrendingDown className="h-8 w-8 text-red-600" />
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Total Operations</p>
                <p className="text-2xl font-bold text-blue-600">
                  {parseInt(summary?.total_operations || 0).toLocaleString()}
                </p>
              </div>
              <Activity className="h-8 w-8 text-blue-600" />
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Avg Credits/Operation</p>
                <p className="text-2xl font-bold text-green-600">
                  {parseFloat(summary?.avg_credits_per_operation || 0).toFixed(2)}
                </p>
              </div>
              <TrendingUp className="h-8 w-8 text-green-600" />
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Success Rate</p>
                <p className="text-2xl font-bold text-green-600">
                  {summary?.total_operations > 0 
                    ? ((summary.successful_operations / summary.total_operations) * 100).toFixed(1)
                    : 0}%
                </p>
              </div>
              <Badge variant="default" className="bg-green-100 text-green-800">
                {summary?.successful_operations || 0}/{summary?.total_operations || 0}
              </Badge>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Charts */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Daily Usage Chart */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center space-x-2">
              <Calendar className="h-5 w-5" />
              <span>Daily Credit Usage</span>
            </CardTitle>
          </CardHeader>
          <CardContent>
            <ResponsiveContainer width="100%" height={300}>
              <LineChart data={dailyData}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="date" />
                <YAxis />
                <Tooltip />
                <Line 
                  type="monotone" 
                  dataKey="usage" 
                  stroke="#8884d8" 
                  strokeWidth={2}
                />
              </LineChart>
            </ResponsiveContainer>
          </CardContent>
        </Card>

        {/* Operation Breakdown */}
        <Card>
          <CardHeader>
            <CardTitle>Usage by Operation Type</CardTitle>
          </CardHeader>
          <CardContent>
            <ResponsiveContainer width="100%" height={300}>
              <PieChart>
                <Pie
                  data={operationData}
                  cx="50%"
                  cy="50%"
                  labelLine={false}
                  label={({ name, percent }) => `${name} ${(percent * 100).toFixed(0)}%`}
                  outerRadius={80}
                  fill="#8884d8"
                  dataKey="credits"
                >
                  {operationData.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                  ))}
                </Pie>
                <Tooltip />
              </PieChart>
            </ResponsiveContainer>
          </CardContent>
        </Card>
      </div>

      {/* Operation Details */}
      <Card>
        <CardHeader>
          <CardTitle>Operation Details</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            {(operationSummary || []).map((operation, index) => (
              <div key={index} className="flex items-center justify-between p-4 border rounded-lg">
                <div className="flex items-center space-x-3">
                  {getOperationIcon(operation.operation_type)}
                  <div>
                    <h4 className="font-medium">{formatOperationType(operation.operation_type)}</h4>
                    <p className="text-sm text-gray-600">
                      {parseInt(operation.operation_count).toLocaleString()} operations
                    </p>
                  </div>
                </div>
                <div className="text-right">
                  <p className="font-medium text-red-600">
                    {parseFloat(operation.total_credits).toLocaleString()} credits
                  </p>
                  <p className="text-sm text-gray-600">
                    Avg: {parseFloat(operation.avg_credits_per_operation).toFixed(2)} per operation
                  </p>
                </div>
              </div>
            ))}
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default CreditUsageAnalytics;
